/* Navbar CSS */

@import url("https://fonts.googleapis.com/css2?family=Muli&display=swap");

*{
    margin :0;
    padding: 0;
    box-sizing: border-box;
    font-family: Muli, sans-serif;
}

.navbar {
    background-color: #fff;
}

.navbar-brand {
    font-size: 1.9rem, !important;
    color: #e6e5f0,!important;
}

.navbar {
    font-size: 1.3rem, !important;
    text-transform: capitalize;
}

.menu_active {
    font-size: bold;
    border-bottom: 1px solid #565387;
}

.navbar a:hover {
    color: #3498db, !important;
}

.navmargin {
    margin-bottom: 100%;
}

.bgfooter {
    background-color: rgb(27, 26, 26);
    color: white;
    position: relative;
    width: 100%;
    height: 60px;
    
}

.footerdiv {
    margin-top: 80%;
}

@media (max-width : 992px) {
    .navbar-brand{
        font-size: 1.5rem;
    }

    .menu_active {
        font-weight: bold;
        border-bottom: none;
    }
}

.logoimg {
    height: 60px;
    width: 60px;
    background-color: transparent;
}

/* Home CSS */

#header {
    width: 100%;
    height: 80vh;
    margin-top: 70px;
}

#header h1 {
    margin: 0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    color: black;
}

#header .brand-name {
    color: #3498db;
}

#header h2 {
    color: #484848;
    font-size: 24px;
}

#header .btn-get-started {
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
}

#header .btn-get-started:hover {
    background: #3498db;
    color: #fff;
}

#header img {
    margin-top: 150px;
    text-align: right;
    height: 350px;
    width: 330px;
}

#header .animated {
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

.aboutimg {
    height: 200px;
    width: 200px;
}

@-webkit-keyframes up-down {
    0% {
        transform: translate(10px);
    }
    100% {
        transform: translate(-10px);
    }
}

@keyframes up-down {
    0% {
        transform: translate(10px);
    }
    100% {
        transform: translate(-10px);
    }
}

.bgnav {
    background-color: rgb(236, 236, 244);
    color: rgb(26, 24, 24);
}
.navtext {
    text-emphasis-color: white;
}

.btnwidth {
    width: 48%;
}

.mrgn {
    margin-top: 60px;
    margin-bottom: 40px;
    color: rgb(4, 4, 99);
}

.imgcards {
    height: 200px;
    width: 100%;
}


